body {
  margin: 0;
  padding: 0;
  font-family: "Helvetica Neue", Helvetica, "Hiragino Sans GB", Arial, sans-serif;
  font-size: 16px;
}

.Background {
  position: fixed;
  right: 0;
  top: 0;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
}
.App {
  text-align: center;
}

.App__border {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 1px solid rgba(0, 0, 0, 0.5);
  margin: 8px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.App__header {
  color: rgba(0, 0, 0, 0.5);
  font-size: 5em;
  font-weight: 100;
}
